export function integertest(rule, value, callback) {
    if (!/^\d+$/.test(value)) {
        callback(new Error(""));
    } else {
        callback();
    }
};
//0-1
export function checknum(rule, value, callback) {
    if (!/^(0.\d+|0|1)$/.test(value)) {
        callback(new Error(""));
    } else {
        callback();
    }
};

